.searchBar-wrap {
  background-color: #fff;
  width: fit-content;
  margin: 2.5rem auto 4rem auto;
  padding: 0.5rem;
  /* border-radius: 23px; */
}

.searchBar-wrap form {
  display: flex;
  align-items: center;
  background-color: #fff;

}

.searchBar-wrap input {
  background-color: #fff;

  color: black;
  width: 300px;
  outline: none;
  border: none;
}

.searchBar-wrap span {
  padding-right: 0.5rem;
  cursor: pointer;
  background-color: #fff;
}

.searchBar-wrap button {
  outline: none;
  border: none;
  padding: 0.3rem 1rem;
  border-radius: 5px;
  background-color: #fbecec;

  color: #000;
}