.blogItem-wrap {
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 15px;

}

.blogItem-author-name {
  background-color: #fff;
  background: #fff;
  color: rgb(75, 75, 75);
}

.blogItem-cover {
  width: 105%;
  height: 250px;
  object-fit: cover;
  /* border-radius: 15px; */
  margin-bottom: 0.5rem;
  margin-left: -10px;

}

.blogItem-wrap h3 {
  margin: 0.5rem 0 1rem 0;
  font-size: 20px;
  flex: 1;
  background-color: rgb(255, 255, 255);

}

.blogItem-desc {
  position: relative;
  max-height: 60px;
  overflow: hidden;
  padding-right: 0.6rem;
  font-size: 0.8rem;
  color: #a9a9a9;
  background-color: rgb(255, 255, 255);

}

.blogItem-desc::before {
  position: absolute;
  content: '...';
  bottom: 0;
  right: 0;

}

.blogItem-wrap footer {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  justify-content: space-between;
  border-radius: 15px;
  background-color: rgb(255, 255, 255);



}

.blogItem-link {
  text-decoration: none;
  color: inherit;
  background-color: #fff;
  border-radius: 15px;


}

.blogItem-author {
  background-color: rgb(255, 255, 255);
  display: flex;
  color: #fff;
  align-items: center;

}

.blogItem-author img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 0.5rem;
  background-color: rgb(255, 255, 255);

}

.blogItem-author p {
  background-color: rgb(255, 255, 255);

  font-size: 0.6rem;
  color: #a9a9a9;
  font-weight: 600;
}

.blogItem-wrap footer {
  background-color: rgb(255, 255, 255);
}