:root {
    --text-color: #f0f4f5;
    --background-color: #263343;
    --accent-color: #d49466;
}

body {
    margin: 0px;
    font-family: 'Open sans';
}

a {
    text-decoration: none;
    color: white;
    font-size: 25px;
    margin-left: 10px;
}


/* .logo {
    background: #263343;
} */

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #263343;
    padding: 12px 12px;
    position: fixed;
    width: 100%;
    z-index: 100;
    padding-top: 10px;
}

.air {
    padding: 10px;
}



@media screen and (max-width: 800px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 8px 24px;
    }
}